import { cache } from '../../cache';
import { DEFAULT_SVG_FONT_SIZE, FILL, STROKE } from '../../constants';
import type { ObjectEvents } from '../../EventTypeDefs';
import type {
  CompleteTextStyleDeclaration,
  TextStyle,
  TextStyleDeclaration,
} from './StyledText';
import { StyledText } from './StyledText';
import { SHARED_ATTRIBUTES } from '../../parser/attributes';
import { parseAttributes } from '../../parser/parseAttributes';
import type {
  Abortable,
  TCacheCanvasDimensions,
  TClassProperties,
  TFiller,
  TOptions,
} from '../../typedefs';
import { classRegistry } from '../../ClassRegistry';
import { graphemeSplit } from '../../util/lang_string';
import { createCanvasElementFor } from '../../util/misc/dom';
import { layoutText, type LayoutResult, type TextLayoutOptions } from '../../text/layout';
import { measureGrapheme, measureGraphemeWithKerning } from '../../text/measure';
import { applyEllipsis } from '../../text/ellipsis';
import { segmentGraphemes, findKashidaPoints, ARABIC_TATWEEL, type KashidaPoint } from '../../text/unicode';
import type { TextStyleArray } from '../../util/misc/textStyles';
import {
  hasStyleChanged,
  stylesFromArray,
  stylesToArray,
} from '../../util/misc/textStyles';
import { getPathSegmentsInfo, getPointOnPath } from '../../util/path';
import { cacheProperties } from '../Object/FabricObject';
import type { Path } from '../Path';
import { TextSVGExportMixin } from './TextSVGExportMixin';
import { applyMixins } from '../../util/applyMixins';
import type { FabricObjectProps, SerializedObjectProps } from '../Object/types';
import type { StylePropertiesType } from './constants';
import {
  additionalProps,
  textDefaultValues,
  textLayoutProperties,
  JUSTIFY,
  JUSTIFY_CENTER,
  JUSTIFY_LEFT,
  JUSTIFY_RIGHT,
  TEXT_DECORATION_THICKNESS,
} from './constants';
import { CENTER, LEFT, RIGHT, TOP, BOTTOM } from '../../constants';
import { isFiller } from '../../util/typeAssertions';
import type { Gradient } from '../../gradient/Gradient';
import type { Pattern } from '../../Pattern';
import type { CSSRules } from '../../parser/typedefs';
import { getBrowserLines, clearBrowserLines } from '../../text/browserLines';
import type { BrowserLine } from '../../text/browserLines';

let measuringContext: CanvasRenderingContext2D | null;

/**
 * Return a context for measurement of text string.
 * if created it gets stored for reuse
 */
function getMeasuringContext() {
  if (!measuringContext) {
    const canvas = createCanvasElementFor({
      width: 0,
      height: 0,
    });
    measuringContext = canvas.getContext('2d');
  }
  return measuringContext;
}

export type TPathSide = 'left' | 'right';

export type TPathAlign = 'baseline' | 'center' | 'ascender' | 'descender';

export type TextLinesInfo = {
  lines: string[];
  graphemeLines: string[][];
  graphemeText: string[];
  _unwrappedLines: string[][];
};

/**
 * Measure and return the info of a single grapheme.
 * needs the the info of previous graphemes already filled
 * Override to customize measuring
 */
export type GraphemeBBox = {
  width: number;
  height: number;
  kernedWidth: number;
  left: number;
  deltaY: number;
  renderLeft?: number;
  renderTop?: number;
  angle?: number;
};

// @TODO this is not complete
interface UniqueTextProps {
  charSpacing: number;
  lineHeight: number;
  fontSize: number;
  fontWeight: string | number;
  fontFamily: string;
  fontStyle: string;
  pathSide: TPathSide;
  pathAlign: TPathAlign;
  underline: boolean;
  overline: boolean;
  linethrough: boolean;
  textAlign: string;
  direction: CanvasDirection;
  path?: Path;
  textDecorationThickness: number;
  wrap: 'word' | 'char' | 'none';
  ellipsis: boolean | string;
  letterSpacing: number;
  enableAdvancedLayout: boolean;
  verticalAlign: 'top' | 'middle' | 'bottom';
  useOverlayEditing: boolean;
  kashida: 'none' | 'short' | 'medium' | 'long' | 'stylistic';
}

export interface SerializedTextProps
  extends SerializedObjectProps,
  UniqueTextProps {
  styles: TextStyleArray | TextStyle;
}

export interface TextProps extends FabricObjectProps, UniqueTextProps {
  styles: TextStyle;
}

/**
 * Text class
 * @see {@link http://fabricjs.com/fabric-intro-part-2#text}
 */
export class FabricText<
  Props extends TOptions<TextProps> = Partial<TextProps>,
  SProps extends SerializedTextProps = SerializedTextProps,
  EventSpec extends ObjectEvents = ObjectEvents,
>
  extends StyledText<Props, SProps, EventSpec>
  implements UniqueTextProps {
  /**
   * Properties that requires a text layout recalculation when changed
   * @type string[]
   * @protected
   */
  static textLayoutProperties: string[] = textLayoutProperties;

  /**
   * @private
   */
  declare _reNewline: RegExp;

  /**
   * Use this regular expression to filter for whitespaces that is not a new line.
   * Mostly used when text is 'justify' aligned.
   * @private
   */
  declare _reSpacesAndTabs: RegExp;

  /**
   * Use this regular expression to filter for whitespace that is not a new line.
   * Mostly used when text is 'justify' aligned.
   * @private
   */
  declare _reSpaceAndTab: RegExp;

  /**
   * Use this regular expression to filter consecutive groups of non spaces.
   * Mostly used when text is 'justify' aligned.
   * @private
   */
  declare _reWords: RegExp;

  declare text: string;

  /**
   * Font size (in pixels)
   * @type Number
   */
  declare fontSize: number;

  /**
   * Font weight (e.g. bold, normal, 400, 600, 800)
   * @type {(Number|String)}
   */
  declare fontWeight: string | number;

  /**
   * Font family
   * @type String
   */
  declare fontFamily: string;

  /**
   * Text decoration underline.
   * @type Boolean
   */
  declare underline: boolean;

  /**
   * Text decoration overline.
   * @type Boolean
   */
  declare overline: boolean;

  /**
   * Text decoration linethrough.
   * @type Boolean
   */
  declare linethrough: boolean;

  /**
   * Text alignment. Possible values: "left", "center", "right", "justify",
   * "justify-left", "justify-center" or "justify-right".
   * @type String
   */
  declare textAlign: string;

  /**
   * Font style . Possible values: "", "normal", "italic" or "oblique".
   * @type String
   */
  declare fontStyle: string;

  /**
   * Line height
   * @type Number
   */
  declare lineHeight: number;

  /**
   * Superscript schema object (minimum overlap)
   */
  declare superscript: {
    /**
     * fontSize factor
     * @default 0.6
     */
    size: number;
    /**
     * baseline-shift factor (upwards)
     * @default -0.35
     */
    baseline: number;
  };

  /**
   * Subscript schema object (minimum overlap)
   */
  declare subscript: {
    /**
     * fontSize factor
     * @default 0.6
     */
    size: number;
    /**
     * baseline-shift factor (downwards)
     * @default 0.11
     */
    baseline: number;
  };

  /**
   * Background color of text lines
   * @type String
   */
  declare textBackgroundColor: string;

  declare styles: TextStyle;

  /**
   * Path that the text should follow.
   * since 4.6.0 the path will be drawn automatically.
   * if you want to make the path visible, give it a stroke and strokeWidth or fill value
   * if you want it to be hidden, assign visible = false to the path.
   * This feature is in BETA, and SVG import/export is not yet supported.
   * @type Path
   * @example
   * const textPath = new Text('Text on a path', {
   *     top: 150,
   *     left: 150,
   *     textAlign: 'center',
   *     charSpacing: -50,
   *     path: new Path('M 0 0 C 50 -100 150 -100 200 0', {
   *         strokeWidth: 1,
   *         visible: false
   *     }),
   *     pathSide: 'left',
   *     pathStartOffset: 0
   * });
   */
  declare path?: Path;

  /**
   * Text wrapping mode
   * @type string
   * @default 'word'
   */
  declare wrap: 'word' | 'char' | 'none';

  /**
   * Ellipsis truncation
   * @type boolean | string
   * @default false
   */
  declare ellipsis: boolean | string;

  /**
   * Letter spacing in pixels (Konva-style)
   * @type number
   * @default 0
   */
  declare letterSpacing: number;

  /**
   * Enable advanced text layout engine
   * @type boolean
   * @default false
   */
  declare enableAdvancedLayout: boolean;

  /**
   * Vertical text alignment
   * @type string
   * @default 'top'
   */
  declare verticalAlign: 'top' | 'middle' | 'bottom';

  /**
   * Use overlay editor for inline text editing instead of hidden textarea.
   * @default false
   */
  declare useOverlayEditing: boolean;

  /**
   * Arabic kashida (tatweel) justification level.
   * Distributes extra space using kashida extensions for justified Arabic text.
   * - 'none': No kashida (default), uses only space expansion
   * - 'short': 25% kashida, 75% space expansion
   * - 'medium': 50% kashida, 50% space expansion
   * - 'long': 75% kashida, 25% space expansion
   * - 'stylistic': 100% kashida, no space expansion
   * @type string
   * @default 'none'
   */
  declare kashida: 'none' | 'short' | 'medium' | 'long' | 'stylistic';

  /**
   * The text decoration tickness for underline, overline and strikethrough
   * The tickness is expressed in thousandths of fontSize ( em ).
   * The original value was 1/15 that translates to 66.6667 thousandths.
   * The choice of unit of measure is to align with charSpacing.
   * You can slim the tickness without issues, while large underline or overline may end up
   * outside the bounding box of the text. In order to fix that a bigger refactor of the code
   * is needed and is out of scope for now. If you need such large overline on the first line
   * of text or large underline on the last line of text, consider disabling caching as a
   * workaround
   * @default 66.667
   */
  declare textDecorationThickness: number;

  /**
   * Offset amount for text path starting position
   * Only used when text has a path
   */
  declare pathStartOffset: number;

  /**
   * Which side of the path the text should be drawn on.
   * Only used when text has a path
   * @type {TPathSide} 'left|right'
   */
  declare pathSide: TPathSide;

  /**
   * How text is aligned to the path. This property determines
   * the perpendicular position of each character relative to the path.
   * (one of "baseline", "center", "ascender", "descender")
   * This feature is in BETA, and its behavior may change
   * @type TPathAlign
   */
  declare pathAlign: TPathAlign;

  /**
   * @private
   */
  declare _fontSizeFraction: number;

  /**
   * @private
   */
  declare offsets: { underline: number; linethrough: number; overline: number };

  /**
   * Text Line proportion to font Size (in pixels)
   * @type Number
   */
  declare _fontSizeMult: number;

  /**
   * additional space between characters
   * expressed in thousands of em unit
   * @type Number
   */
  declare charSpacing: number;

  /**
   * Baseline shift, styles only, keep at 0 for the main text object
   * @type {Number}
   */
  declare deltaY: number;

  /**
   * WARNING: EXPERIMENTAL. NOT SUPPORTED YET
   * determine the direction of the text.
   * This has to be set manually together with textAlign and originX for proper
   * experience.
   * some interesting link for the future
   * https://www.w3.org/International/questions/qa-bidi-unicode-controls
   * @since 4.5.0
   * @type {CanvasDirection} 'ltr|rtl'
   */
  declare direction: CanvasDirection;

  /**
   * contains characters bounding boxes
   * This variable is considered to be protected.
   * But for how mixins are implemented right now, we can't leave it private
   * @protected
   */
  __charBounds: GraphemeBBox[][] = [];

  /**
   * contains kashida extension info for each line.
   * Each entry contains { charIndex, width } for characters that have kashida extensions.
   * @protected
   */
  __kashidaInfo: Array<Array<{ charIndex: number; width: number; tatweelCount?: number }>> = [];

  /**
   * use this size when measuring text. To avoid IE11 rounding errors
   * @type {Number}
   * @readonly
   * @private
   */
  declare CACHE_FONT_SIZE: number;

  /**
   * contains the min text width to avoid getting 0
   * @type {Number}
   */
  declare MIN_TEXT_WIDTH: number;

  /**
   * contains the the text of the object, divided in lines as they are displayed
   * on screen. Wrapping will divide the text independently of line breaks
   * @type {string[]}
   */
  declare textLines: string[];

  /**
   * same as textlines, but each line is an array of graphemes as split by splitByGrapheme
   * @type {string[]}
   */
  declare _textLines: string[][];

  declare _unwrappedTextLines: string[][];
  declare _text: string[];
  declare cursorWidth: number;
  declare __lineHeights: number[];
  declare __lineWidths: number[];
  declare initialized?: true;

  static cacheProperties = [...cacheProperties, ...additionalProps];

  static ownDefaults = textDefaultValues;

  static type = 'Text';

  static getDefaults(): Record<string, any> {
    return { ...super.getDefaults(), ...FabricText.ownDefaults };
  }

  constructor(text: string, options?: Props) {
    super();
    Object.assign(this, FabricText.ownDefaults);
    this.setOptions(options);
    if (!this.styles) {
      this.styles = {};
    }
    this.text = text;
    this.initialized = true;
    if (this.path) {
      this.setPathInfo();
    }
    this.initDimensions();
    this.setCoords();
  }

  /**
   * If text has a path, it will add the extra information needed
   * for path and text calculations
   */
  setPathInfo() {
    const path = this.path;
    if (path) {
      path.segmentsInfo = getPathSegmentsInfo(path.path);
    }
  }

  /**
   * @private
   * Divides text into lines of text and lines of graphemes.
   * Uses browser lines when available for pixel-perfect consistency.
   */
  _splitText(): TextLinesInfo {
    // Check if we have valid browser lines and should use them
    const browserLines = getBrowserLines(this);
    if (browserLines && this.useOverlayEditing) {
      return this._splitTextFromBrowserLines(browserLines);
    }

    const newLines = this._splitTextIntoLines(this.text);
    this.textLines = newLines.lines;
    this._textLines = newLines.graphemeLines;
    this._unwrappedTextLines = newLines._unwrappedLines;
    this._text = newLines.graphemeText;
    return newLines;
  }

  /**
   * Create TextLinesInfo from browser-extracted lines
   * @private
   */
  _splitTextFromBrowserLines(browserLines: BrowserLine[]): TextLinesInfo {
    const lines: string[] = [];
    const graphemeLines: string[][] = [];
    const unwrappedLines: string[][] = [];
    let graphemeText: string[] = [];

    for (const browserLine of browserLines) {
      lines.push(browserLine.text);
      const lineGraphemes = this.graphemeSplit(browserLine.text);
      graphemeLines.push(lineGraphemes);
      unwrappedLines.push(lineGraphemes);
      graphemeText = graphemeText.concat(lineGraphemes);

      // Add newline separator between lines (except for the last line)
      if (browserLine !== browserLines[browserLines.length - 1]) {
        graphemeText.push('\n');
      }
    }

    const result: TextLinesInfo = {
      lines,
      graphemeLines,
      graphemeText,
      _unwrappedLines: unwrappedLines,
    };

    // Update instance properties
    this.textLines = result.lines;
    this._textLines = result.graphemeLines;
    this._unwrappedTextLines = result._unwrappedLines;
    this._text = result.graphemeText;

    return result;
  }

  /**
   * Initialize or update text dimensions.
   * Updates this.width and this.height with the proper values.
   * Does not return dimensions.
   */
  initDimensions(): void {
    // Check if font is ready for accurate measurements
    // Only block initialization if it's a critical font loading situation
    const fontReady = this._isFontReady();
    if (!fontReady && !this.initialized) {
      // Only schedule font loading on first initialization
      this._scheduleInitAfterFontLoad();
      // Continue with fallback measurements for now
    }

    // Use advanced layout if enabled
    if (this.enableAdvancedLayout && !this.path) {
      return this.initDimensionsAdvanced();
    }

    this._splitText();
    this._clearCache();
    this.dirty = true;
    if (this.path) {
      this.width = this.path.width;
      this.height = this.path.height;
    } else {
      this.width =
        this.calcTextWidth() || this.cursorWidth || this.MIN_TEXT_WIDTH;
      this.height = this.calcTextHeight();
    }
    if (this.textAlign.includes(JUSTIFY)) {
      // once text is measured we need to make space fatter to make justified text.
      if (this.__charBounds && this.__charBounds.length > 0) {
        this.enlargeSpaces();
      }
    }
  }

  /**
   * Enlarge space boxes and shift the others for justify alignment.
   * Supports Arabic kashida (tatweel) justification when kashida property is set.
   * When kashida is enabled, actual tatweel characters are inserted into the text.
   */
  enlargeSpaces() {
    // console.log('=== enlargeSpaces START ===');
    // console.log('this.kashida:', this.kashida);

    // Kashida ratios: proportion of extra space distributed via kashida vs space expansion
    const kashidaRatios: Record<string, number> = {
      none: 0,
      short: 0.25,
      medium: 0.5,
      long: 0.75,
      stylistic: 1.0,
    };
    const kashidaRatio = kashidaRatios[this.kashida] || 0;
    // console.log('kashidaRatio:', kashidaRatio);

    // Reset kashida info
    this.__kashidaInfo = [];

    for (let i = 0, len = this._textLines.length; i < len; i++) {
      // Initialize kashida info for this line
      this.__kashidaInfo[i] = [];

      // Check if this line should be justified
      const hasTextAfter = this._textLines
        .slice(i + 1)
        .some((line) => {
          const lineText = Array.isArray(line) ? line.join('') : line;
          return /\S/.test(lineText);
        });
      const isVisualLastLine = !hasTextAfter;
      const isLastLine =
        i === len - 1 || this.isEndOfWrapping(i) || isVisualLastLine;
      const shouldJustifyLine =
        this.textAlign.includes('justify') && !isLastLine;

      if (!shouldJustifyLine) {
        // console.log(`  Line ${i}: skipped (not justified)`);
        continue;
      }

      const line = this._textLines[i];
      const currentLineWidth = this.getLineWidth(i);
      const totalExtraSpace = this.width - currentLineWidth;
      // console.log(`  Line ${i}: width=${this.width}, lineWidth=${currentLineWidth}, extraSpace=${totalExtraSpace}`);

      if (totalExtraSpace <= 0) {
        // console.log(`  Line ${i}: skipped (no extra space)`);
        continue;
      }

      // Find spaces for space expansion
      const spaces = this.textLines[i].match(this._reSpacesAndTabs);
      const numberOfSpaces = spaces ? spaces.length : 0;

      // Find kashida points if enabled
      const kashidaPoints = kashidaRatio > 0 ? findKashidaPoints(line) : [];
      const hasKashidaPoints = kashidaPoints.length > 0;

      // Calculate space distribution
      let kashidaSpace = 0;
      let spaceExpansion = totalExtraSpace;

      if (hasKashidaPoints && kashidaRatio > 0) {
        // Distribute between kashida and spaces
        kashidaSpace = totalExtraSpace * kashidaRatio;
        spaceExpansion = totalExtraSpace * (1 - kashidaRatio);
      }

      // Calculate per-kashida and per-space widths
      const perKashidaWidth = hasKashidaPoints ? kashidaSpace / kashidaPoints.length : 0;
      const perSpaceWidth = numberOfSpaces > 0 ? spaceExpansion / numberOfSpaces : 0;

      // If kashida is enabled, insert tatweel characters into the text
      if (hasKashidaPoints && perKashidaWidth > 0) {
        // console.log(`=== Inserting kashida for line ${i} ===`);
        // console.log(`  kashidaPoints: ${kashidaPoints.length}, perKashidaWidth: ${perKashidaWidth}`);

        // Sort by charIndex descending to insert from end (so indices stay valid)
        const sortedPoints = [...kashidaPoints].sort((a, b) => b.charIndex - a.charIndex);

        // Calculate how many tatweels to insert per point
        // Measure tatweel width to determine count
        const ctx = getMeasuringContext();
        // console.log(`  getMeasuringContext: ${ctx ? 'OK' : 'NULL'}`);

        if (ctx) {
          ctx.font = this._getFontDeclaration();
          const tatweelWidth = ctx.measureText(ARABIC_TATWEEL).width;
          // console.log(`  tatweelWidth: ${tatweelWidth}`);

          if (tatweelWidth > 0) {
            const newLine = [...line];
            let insertedCount = 0;

            for (const point of sortedPoints) {
              const tatweelCount = Math.max(1, Math.round(perKashidaWidth / tatweelWidth));
              // console.log(`  Point ${point.charIndex}: inserting ${tatweelCount} tatweels`);

              // Insert tatweels after the character
              for (let t = 0; t < tatweelCount; t++) {
                newLine.splice(point.charIndex + 1, 0, ARABIC_TATWEEL);
                insertedCount++;
              }

              // Store kashida info with updated indices and tatweel count
              this.__kashidaInfo[i].push({
                charIndex: point.charIndex,
                width: perKashidaWidth,
                tatweelCount: tatweelCount,
              });
            }

            // console.log(`  Total inserted: ${insertedCount} tatweels`);
            // console.log(`  Original line length: ${line.length}, new line length: ${newLine.length}`);
            // console.log(`  New line: ${newLine.join('')}`);

            // Update _textLines with the new line containing tatweels
            this._textLines[i] = newLine;

            // Update textLines string version
            if (this.textLines && this.textLines[i] !== undefined) {
              (this as any).textLines[i] = newLine.join('');
            }

            // Recalculate charBounds for this line since text changed
            this.__charBounds[i] = [];
            this.__lineWidths[i] = undefined as any;
            this._measureLine(i);

            // console.log(`  After remeasure, lineWidth: ${this.__lineWidths[i]}`);
          }
        }
      }

      // Now apply space expansion to remaining extra space
      const newLineWidth = this.getLineWidth(i);
      const remainingSpace = this.width - newLineWidth;

      if (remainingSpace > 0 && numberOfSpaces > 0) {
        const extraPerSpace = remainingSpace / numberOfSpaces;
        let accumulatedOffset = 0;

        for (let j = 0; j < this._textLines[i].length; j++) {
          const charBound = this.__charBounds[i][j];
          if (!charBound) continue;

          charBound.left += accumulatedOffset;

          if (this._reSpaceAndTab.test(this._textLines[i][j])) {
            charBound.width += extraPerSpace;
            charBound.kernedWidth += extraPerSpace;
            accumulatedOffset += extraPerSpace;
          }
        }
      }
    }

    // Final debug log showing kashida state
    // console.log('=== enlargeSpaces END ===');
    // console.log('Final __kashidaInfo:', JSON.stringify(this.__kashidaInfo.map((lineInfo, i) => ({
    //   line: i,
    //   entries: lineInfo.map(k => ({ charIndex: k.charIndex, tatweelCount: k.tatweelCount }))
    // }))));
  }

  /**
   * Advanced layout using new text engine (Konva-compatible)
   * @private
   */
  _layoutTextAdvanced(): LayoutResult {
    const options = this._getAdvancedLayoutOptions();
    return layoutText(options);
  }

  /**
   * Get advanced layout options from current text properties
   * @private
   */
  _getAdvancedLayoutOptions(): TextLayoutOptions {
    return {
      text: this.text,
      width: this.width,
      // Don't pass height constraint to allow vertical auto-expansion
      // Only pass height if ellipsis is enabled (need to truncate)
      height: this.ellipsis ? this.height : undefined,
      wrap: this.wrap || 'word',
      align: this._mapTextAlignToAlign(this.textAlign),
      ellipsis: this.ellipsis || false,
      fontSize: this.fontSize,
      lineHeight: this.lineHeight,
      letterSpacing: this.letterSpacing || 0,
      charSpacing: this.charSpacing,
      direction: this.direction === 'inherit' ? 'ltr' : this.direction,
      fontFamily: this.fontFamily,
      fontStyle: this.fontStyle,
      fontWeight: this.fontWeight,
      verticalAlign: this.verticalAlign || 'top',
    };
  }

  /**
   * Map Fabric textAlign to Konva align format
   * @private
   */
  _mapTextAlignToAlign(textAlign: string): 'left' | 'center' | 'right' | 'justify' {
    switch (textAlign) {
      case 'center':
      case CENTER:
        return 'center';
      case 'right':
      case RIGHT:
        return 'right';
      case 'justify':
      case JUSTIFY:
      case JUSTIFY_LEFT:
      case JUSTIFY_RIGHT:
      case JUSTIFY_CENTER:
        return 'justify';
      default:
        return 'left';
    }
  }

  /**
   * Enhanced initDimensions that uses advanced layout when enabled
   */
  initDimensionsAdvanced(): void {
    if (!this.enableAdvancedLayout) {
      return this.initDimensions();
    }

    const layout = this._layoutTextAdvanced();

    // Update dimensions from layout
    this.width = layout.totalWidth || this.MIN_TEXT_WIDTH;
    this.height = layout.totalHeight;

    // Convert layout to legacy format for compatibility
    this._convertLayoutToLegacyFormat(layout);

    // Apply kashida if enabled for justify alignment
    // This must be called after _convertLayoutToLegacyFormat to ensure __charBounds exists
    if (this.textAlign.includes(JUSTIFY) && this.kashida && this.kashida !== 'none') {
      if (this.__charBounds && this.__charBounds.length > 0) {
        this.enlargeSpaces();
      }
    }

    this.dirty = true;
  }

  /**
   * Convert new layout format to legacy _textLines and __charBounds format
   * @private
   */
  _convertLayoutToLegacyFormat(layout: LayoutResult): void {
    this._textLines = layout.lines.map(line => line.graphemes);
    (this as any).textLines = layout.lines.map(line => line.text);

    // Set _text as flat array of all graphemes (required for editing)
    this._text = layout.lines.flatMap(line => line.graphemes);

    // Convert bounds to legacy format
    // IMPORTANT: Preserve both logical (left) and visual (renderLeft) positions
    // - left: cumulative logical offset (for text editing operations)
    // - renderLeft: actual visual X position after BiDi reordering and alignment
    // The renderLeft is critical for correct cursor/selection hit testing in mixed RTL/LTR text
    this.__charBounds = layout.lines.map(line =>
      line.bounds.map(bound => ({
        left: bound.left,
        top: bound.y,
        width: bound.width,
        height: bound.height,
        kernedWidth: bound.kernedWidth,
        deltaY: bound.deltaY || 0,
        renderLeft: bound.x, // Visual X position for hit testing
      }))
    );

    // Populate line widths cache to prevent getLineWidth from triggering legacy measurement
    this.__lineWidths = layout.lines.map(line => line.width);

    // Update grapheme info for compatibility
    if (layout.lines.length > 0) {
      (this as any)._unwrappedTextLines = layout.lines.map(line => line.graphemes);
    }
  }

  /**
   * Detect if the text line is ended with an hard break
   * text and itext do not have wrapping, return false
   * @return {Boolean}
   */
  isEndOfWrapping(lineIndex: number): boolean {
    return lineIndex === this._textLines.length - 1;
  }

  /**
   * Detect if a line has a linebreak and so we need to account for it when moving
   * and counting style.
   * It return always 1 for text and Itext. Textbox has its own implementation
   * @return Number
   */
  missingNewlineOffset(lineIndex: number, skipWrapping?: boolean): 0 | 1;
  missingNewlineOffset(_lineIndex: number): 1 {
    return 1;
  }

  /**
   * Returns 2d representation (lineIndex and charIndex) of cursor
   * @param {Number} selectionStart
   * @param {Boolean} [skipWrapping] consider the location for unwrapped lines. useful to manage styles.
   */
  get2DCursorLocation(selectionStart: number, skipWrapping?: boolean) {
    const lines = skipWrapping ? this._unwrappedTextLines : this._textLines;
    let i: number;
    for (i = 0; i < lines.length; i++) {
      if (selectionStart <= lines[i].length) {
        return {
          lineIndex: i,
          charIndex: selectionStart,
        };
      }
      selectionStart -=
        lines[i].length + this.missingNewlineOffset(i, skipWrapping);
    }
    return {
      lineIndex: i - 1,
      charIndex:
        lines[i - 1].length < selectionStart
          ? lines[i - 1].length
          : selectionStart,
    };
  }

  /**
   * Returns string representation of an instance
   * @return {String} String representation of text object
   */
  toString(): string {
    return `#<Text (${this.complexity()}): { "text": "${this.text
      }", "fontFamily": "${this.fontFamily}" }>`;
  }

  /**
   * Return the dimension and the zoom level needed to create a cache canvas
   * big enough to host the object to be cached.
   * @private
   * @param {Object} dim.x width of object to be cached
   * @param {Object} dim.y height of object to be cached
   * @return {Object}.width width of canvas
   * @return {Object}.height height of canvas
   * @return {Object}.zoomX zoomX zoom value to unscale the canvas before drawing cache
   * @return {Object}.zoomY zoomY zoom value to unscale the canvas before drawing cache
   */
  _getCacheCanvasDimensions(): TCacheCanvasDimensions {
    const dims = super._getCacheCanvasDimensions();
    const fontSize = this.fontSize;
    dims.width += fontSize * dims.zoomX;
    dims.height += fontSize * dims.zoomY;
    return dims;
  }

  /**
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  _render(ctx: CanvasRenderingContext2D) {
    const path = this.path;
    path && !path.isNotVisible() && path._render(ctx);
    this._setTextStyles(ctx);
    this._renderTextLinesBackground(ctx);
    this._renderTextDecoration(ctx, 'underline');
    this._renderText(ctx);
    this._renderTextDecoration(ctx, 'overline');
    this._renderTextDecoration(ctx, 'linethrough');
  }

  /**
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  _renderText(ctx: CanvasRenderingContext2D) {
    // Skip text rendering if in overlay editing mode
    if ((this as any).__overlayEditor) {
      return;
    }
    if (this.paintFirst === STROKE) {
      this._renderTextStroke(ctx);
      this._renderTextFill(ctx);
    } else {
      this._renderTextFill(ctx);
      this._renderTextStroke(ctx);
    }
  }

  /**
   * Set the font parameter of the context with the object properties or with charStyle
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   * @param {Object} [charStyle] object with font style properties
   * @param {String} [charStyle.fontFamily] Font Family
   * @param {Number} [charStyle.fontSize] Font size in pixels. ( without px suffix )
   * @param {String} [charStyle.fontWeight] Font weight
   * @param {String} [charStyle.fontStyle] Font style (italic|normal)
   */
  _setTextStyles(
    ctx: CanvasRenderingContext2D,
    charStyle?: any,
    forMeasuring?: boolean,
  ) {
    ctx.textBaseline = 'alphabetic';
    if (this.path) {
      switch (this.pathAlign) {
        case CENTER:
          ctx.textBaseline = 'middle';
          break;
        case 'ascender':
          ctx.textBaseline = TOP;
          break;
        case 'descender':
          ctx.textBaseline = BOTTOM;
          break;
      }
    }
    ctx.font = this._getFontDeclaration(charStyle, forMeasuring);
  }

  /**
   * calculate and return the text Width measuring each line.
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   * @return {Number} Maximum width of Text object
   */
  calcTextWidth(): number {
    let maxWidth = this.getLineWidth(0);

    for (let i = 1, len = this._textLines.length; i < len; i++) {
      const currentLineWidth = this.getLineWidth(i);
      if (currentLineWidth > maxWidth) {
        maxWidth = currentLineWidth;
      }
    }
    return maxWidth;
  }

  /**
   * @private
   * @param {String} method Method name ("fillText" or "strokeText")
   * @param {CanvasRenderingContext2D} ctx Context to render on
   * @param {String} line Text to render
   * @param {Number} left Left position of text
   * @param {Number} top Top position of text
   * @param {Number} lineIndex Index of a line in a text
   */
  _renderTextLine(
    method: 'fillText' | 'strokeText',
    ctx: CanvasRenderingContext2D,
    line: string[],
    left: number,
    top: number,
    lineIndex: number,
  ) {
    this._renderChars(method, ctx, line, left, top, lineIndex);
  }

  /**
   * Build display text lines with kashida characters inserted.
   * This creates a version of _textLines with tatweel characters added at kashida points.
   * @private
   */
  _buildKashidaDisplayLines(): string[][] {
    if (this.kashida === 'none' || !this.__kashidaInfo) {
      return this._textLines;
    }

    const displayLines: string[][] = [];

    for (let lineIndex = 0; lineIndex < this._textLines.length; lineIndex++) {
      const line = this._textLines[lineIndex];
      const kashidaInfo = this.__kashidaInfo[lineIndex];

      if (!kashidaInfo || kashidaInfo.length === 0) {
        displayLines.push([...line]);
        continue;
      }

      // Sort kashida points by charIndex descending so we can insert from the end
      const sortedKashida = [...kashidaInfo].sort((a, b) => b.charIndex - a.charIndex);

      // Calculate how many tatweels to insert based on width
      const newLine = [...line];
      for (const { charIndex, width } of sortedKashida) {
        if (width <= 0 || charIndex >= newLine.length) continue;

        // Calculate number of tatweel characters based on width
        // Each tatweel is approximately 5px at font size 24
        const tatweelCount = Math.max(1, Math.round(width / 3));
        const tatweels = ARABIC_TATWEEL.repeat(tatweelCount);

        // Insert tatweels after the character at charIndex
        newLine.splice(charIndex + 1, 0, tatweels);
      }

      displayLines.push(newLine);
    }

    return displayLines;
  }

  /**
   * Renders the text background for lines, taking care of style
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  _renderTextLinesBackground(ctx: CanvasRenderingContext2D) {
    if (!this.textBackgroundColor && !this.styleHas('textBackgroundColor')) {
      return;
    }
    const originalFill = ctx.fillStyle,
      leftOffset = this._getLeftOffset();
    let lineTopOffset = this._getTopOffset();

    for (let i = 0, len = this._textLines.length; i < len; i++) {
      const heightOfLine = this.getHeightOfLine(i);
      if (
        !this.textBackgroundColor &&
        !this.styleHas('textBackgroundColor', i)
      ) {
        lineTopOffset += heightOfLine;
        continue;
      }
      const jlen = this._textLines[i].length;
      const lineLeftOffset = this._getLineLeftOffset(i);
      let boxWidth = 0;
      let boxStart = 0;
      let drawStart;
      let currentColor;
      let lastColor = this.getValueOfPropertyAt(i, 0, 'textBackgroundColor');
      for (let j = 0; j < jlen; j++) {
        // at this point charbox are either standard or full with pathInfo if there is a path.
        const charBox = this.__charBounds[i][j] as Required<GraphemeBBox>;
        currentColor = this.getValueOfPropertyAt(i, j, 'textBackgroundColor');
        if (this.path) {
          ctx.save();
          ctx.translate(charBox.renderLeft, charBox.renderTop);
          ctx.rotate(charBox.angle);
          ctx.fillStyle = currentColor;
          currentColor &&
            ctx.fillRect(
              -charBox.width / 2,
              (-heightOfLine / this.lineHeight) * (1 - this._fontSizeFraction),
              charBox.width,
              heightOfLine / this.lineHeight,
            );
          ctx.restore();
        } else if (currentColor !== lastColor) {
          drawStart = leftOffset + lineLeftOffset + boxStart;
          if (this.direction === 'rtl') {
            drawStart = this.width - drawStart - boxWidth;
          }
          ctx.fillStyle = lastColor;
          lastColor &&
            ctx.fillRect(
              drawStart,
              lineTopOffset,
              boxWidth,
              heightOfLine / this.lineHeight,
            );
          boxStart = charBox.left;
          boxWidth = charBox.width;
          lastColor = currentColor;
        } else {
          boxWidth += charBox.kernedWidth;
        }
      }
      if (currentColor && !this.path) {
        drawStart = leftOffset + lineLeftOffset + boxStart;
        if (this.direction === 'rtl') {
          drawStart = this.width - drawStart - boxWidth;
        }
        ctx.fillStyle = currentColor;
        ctx.fillRect(
          drawStart,
          lineTopOffset,
          boxWidth,
          heightOfLine / this.lineHeight,
        );
      }
      lineTopOffset += heightOfLine;
    }
    ctx.fillStyle = originalFill;
    // if there is text background color no
    // other shadows should be casted
    this._removeShadow(ctx);
  }

  /**
   * measure and return the width of a single character.
   * possibly overridden to accommodate different measure logic or
   * to hook some external lib for character measurement
   * @private
   * @param {String} _char, char to be measured
   * @param {Object} charStyle style of char to be measured
   * @param {String} [previousChar] previous char
   * @param {Object} [prevCharStyle] style of previous char
   */
  _measureChar(
    _char: string,
    charStyle: CompleteTextStyleDeclaration,
    previousChar: string | undefined,
    prevCharStyle: CompleteTextStyleDeclaration | Record<string, never>,
  ) {
    const fontCache = cache.getFontCache(charStyle),
      fontDeclaration = this._getFontDeclaration(charStyle),
      couple = previousChar + _char,
      // Skip kerning for tatweel (kashida) characters - they extend connections
      // and kerning would make the following character appear too narrow
      isTatweel = previousChar === '\u0640',
      stylesAreEqual =
        previousChar &&
        !isTatweel &&
        fontDeclaration === this._getFontDeclaration(prevCharStyle),
      fontMultiplier = charStyle.fontSize / this.CACHE_FONT_SIZE;
    let width: number | undefined,
      coupleWidth: number | undefined,
      previousWidth: number | undefined,
      kernedWidth: number | undefined;

    if (previousChar && !isTatweel && fontCache[previousChar] !== undefined) {
      previousWidth = fontCache[previousChar];
    }
    if (fontCache[_char] !== undefined) {
      kernedWidth = width = fontCache[_char];
    }
    if (stylesAreEqual && fontCache[couple] !== undefined) {
      coupleWidth = fontCache[couple];
      kernedWidth = coupleWidth - previousWidth!;
    }
    if (
      width === undefined ||
      previousWidth === undefined ||
      coupleWidth === undefined
    ) {
      const ctx = getMeasuringContext()!;
      // send a TRUE to specify measuring font size CACHE_FONT_SIZE
      this._setTextStyles(ctx, charStyle, true);
      if (width === undefined) {
        kernedWidth = width = ctx.measureText(_char).width;
        fontCache[_char] = width;
      }
      if (previousWidth === undefined && stylesAreEqual && previousChar && !isTatweel) {
        previousWidth = ctx.measureText(previousChar).width;
        fontCache[previousChar] = previousWidth;
      }
      if (stylesAreEqual && coupleWidth === undefined && !isTatweel) {
        // we can measure the kerning couple and subtract the width of the previous character
        coupleWidth = ctx.measureText(couple).width;
        fontCache[couple] = coupleWidth;
        // safe to use the non-null since if undefined we defined it before.
        kernedWidth = coupleWidth - previousWidth!;
      }
    }
    return {
      width: width * fontMultiplier,
      kernedWidth: kernedWidth! * fontMultiplier,
    };
  }

  /**
   * Computes height of character at given position
   * @param {Number} line the line index number
   * @param {Number} _char the character index number
   * @return {Number} fontSize of the character
   */
  getHeightOfChar(line: number, _char: number): number {
    return this.getValueOfPropertyAt(line, _char, 'fontSize');
  }

  /**
   * measure a text line measuring all characters.
   * @param {Number} lineIndex line number
   */
  measureLine(lineIndex: number) {
    const lineInfo = this._measureLine(lineIndex);
    if (this.charSpacing !== 0) {
      lineInfo.width -= this._getWidthOfCharSpacing();
    }
    if (lineInfo.width < 0) {
      lineInfo.width = 0;
    }
    return lineInfo;
  }

  /**
   * measure every grapheme of a line, populating __charBounds
   * @param {Number} lineIndex
   * @return {Object} object.width total width of characters
   * @return {Object} object.numOfSpaces length of chars that match this._reSpacesAndTabs
   */
  _measureLine(lineIndex: number) {
    // Debug: detect if measureLine is called after justify was applied
    if ((this as any)._justifyApplied) {
      // console.warn(`WARNING: _measureLine called for line ${lineIndex} AFTER justify was applied! This will overwrite justified charBounds.`);
      // console.trace('Stack trace:');
    }

    let width = 0,
      prevGrapheme: string | undefined,
      graphemeInfo: GraphemeBBox | undefined;

    const reverse = this.pathSide === RIGHT,
      path = this.path,
      line = this._textLines[lineIndex],
      llength = line.length,
      lineBounds = new Array<GraphemeBBox>(llength);

    this.__charBounds[lineIndex] = lineBounds;
    for (let i = 0; i < llength; i++) {
      const grapheme = line[i];
      graphemeInfo = this._getGraphemeBox(grapheme, lineIndex, i, prevGrapheme);
      lineBounds[i] = graphemeInfo;
      width += graphemeInfo.kernedWidth;
      prevGrapheme = grapheme;
    }
    // this latest bound box represent the last character of the line
    // to simplify cursor handling in interactive mode.
    lineBounds[llength] = {
      left: graphemeInfo ? graphemeInfo.left + graphemeInfo.width : 0,
      width: 0,
      kernedWidth: 0,
      height: this.fontSize,
      deltaY: 0,
    } as GraphemeBBox;
    if (path && path.segmentsInfo) {
      let positionInPath = 0;
      const totalPathLength =
        path.segmentsInfo[path.segmentsInfo.length - 1].length;
      switch (this.textAlign) {
        case LEFT:
          positionInPath = reverse ? totalPathLength - width : 0;
          break;
        case CENTER:
          positionInPath = (totalPathLength - width) / 2;
          break;
        case RIGHT:
          positionInPath = reverse ? 0 : totalPathLength - width;
          break;
        //todo - add support for justify
      }
      positionInPath += this.pathStartOffset * (reverse ? -1 : 1);
      for (
        let i = reverse ? llength - 1 : 0;
        reverse ? i >= 0 : i < llength;
        reverse ? i-- : i++
      ) {
        graphemeInfo = lineBounds[i];
        if (positionInPath > totalPathLength) {
          positionInPath %= totalPathLength;
        } else if (positionInPath < 0) {
          positionInPath += totalPathLength;
        }
        // it would probably much faster to send all the grapheme position for a line
        // and calculate path position/angle at once.
        this._setGraphemeOnPath(positionInPath, graphemeInfo);
        positionInPath += graphemeInfo.kernedWidth;
      }
    }
    return { width: width, numOfSpaces: 0 };
  }

  /**
   * Calculate the angle  and the left,top position of the char that follow a path.
   * It appends it to graphemeInfo to be reused later at rendering
   * @private
   * @param {Number} positionInPath to be measured
   * @param {GraphemeBBox} graphemeInfo current grapheme box information
   * @param {Object} startingPoint position of the point
   */
  _setGraphemeOnPath(positionInPath: number, graphemeInfo: GraphemeBBox) {
    const centerPosition = positionInPath + graphemeInfo.kernedWidth / 2,
      path = this.path!;

    // we are at currentPositionOnPath. we want to know what point on the path is.
    const info = getPointOnPath(path.path, centerPosition, path.segmentsInfo)!;
    graphemeInfo.renderLeft = info.x - path.pathOffset.x;
    graphemeInfo.renderTop = info.y - path.pathOffset.y;
    graphemeInfo.angle = info.angle + (this.pathSide === RIGHT ? Math.PI : 0);
  }

  /**
   *
   * @param {String} grapheme to be measured
   * @param {Number} lineIndex index of the line where the char is
   * @param {Number} charIndex position in the line
   * @param {String} [prevGrapheme] character preceding the one to be measured
   * @returns {GraphemeBBox} grapheme bbox
   */
  _getGraphemeBox(
    grapheme: string,
    lineIndex: number,
    charIndex: number,
    prevGrapheme?: string,
    skipLeft?: boolean,
  ): GraphemeBBox {
    const style = this.getCompleteStyleDeclaration(lineIndex, charIndex),
      prevStyle = prevGrapheme
        ? this.getCompleteStyleDeclaration(lineIndex, charIndex - 1)
        : {},
      info = this._measureChar(grapheme, style, prevGrapheme, prevStyle);
    let kernedWidth = info.kernedWidth,
      width = info.width,
      charSpacing;

    if (this.charSpacing !== 0) {
      charSpacing = this._getWidthOfCharSpacing();
      width += charSpacing;
      kernedWidth += charSpacing;
    }

    const box: GraphemeBBox = {
      width,
      left: 0,
      height: style.fontSize,
      kernedWidth,
      deltaY: style.deltaY,
    };
    if (charIndex > 0 && !skipLeft) {
      const previousBox = this.__charBounds[lineIndex][charIndex - 1];
      box.left =
        previousBox.left + previousBox.width + info.kernedWidth - info.width;
    }
    return box;
  }

  /**
   * Calculate height of line at 'lineIndex'
   * @param {Number} lineIndex index of line to calculate
   * @return {Number}
   */
  getHeightOfLine(lineIndex: number): number {
    if (this.__lineHeights[lineIndex]) {
      return this.__lineHeights[lineIndex];
    }

    // char 0 is measured before the line cycle because it needs to char
    // emptylines
    let maxHeight = this.getHeightOfChar(lineIndex, 0);
    for (let i = 1, len = this._textLines[lineIndex].length; i < len; i++) {
      maxHeight = Math.max(this.getHeightOfChar(lineIndex, i), maxHeight);
    }

    return (this.__lineHeights[lineIndex] =
      maxHeight * this.lineHeight * this._fontSizeMult);
  }

  /**
   * Calculate text box height
   */
  calcTextHeight() {
    let lineHeight,
      height = 0;
    for (let i = 0, len = this._textLines.length; i < len; i++) {
      lineHeight = this.getHeightOfLine(i);
      height += i === len - 1 ? lineHeight / this.lineHeight : lineHeight;
    }
    return height;
  }

  /**
   * @private
   * @return {Number} Left offset
   */
  _getLeftOffset(): number {
    return this.direction === 'ltr' ? -this.width / 2 : this.width / 2;
  }

  /**
   * @private
   * @return {Number} Top offset
   */
  _getTopOffset(): number {
    return -this.height / 2;
  }

  /**
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   * @param {String} method Method name ("fillText" or "strokeText")
   */
  _renderTextCommon(
    ctx: CanvasRenderingContext2D,
    method: 'fillText' | 'strokeText',
  ) {
    ctx.save();
    let lineHeights = 0;
    const left = this._getLeftOffset(),
      top = this._getTopOffset();

    // Debug: log once per render
    if (method === 'fillText' && this.textAlign?.includes('justify')) {
      // console.log('=== RENDER DEBUG ===');
      // console.log('direction:', this.direction);
      // console.log('textAlign:', this.textAlign);
      // console.log('width:', this.width);
      // console.log('_getLeftOffset:', left);
    }

    for (let i = 0, len = this._textLines.length; i < len; i++) {
      const heightOfLine = this.getHeightOfLine(i),
        maxHeight = heightOfLine / this.lineHeight,
        leftOffset = this._getLineLeftOffset(i);

      // Debug: log line offsets for justify
      if (method === 'fillText' && this.textAlign?.includes('justify')) {
        const lineWidth = this.getLineWidth(i);
        // console.log(`Line ${i}: leftOffset=${leftOffset.toFixed(2)}, lineWidth=${lineWidth.toFixed(2)}, renderAt=${(left + leftOffset).toFixed(2)}`);
      }

      this._renderTextLine(
        method,
        ctx,
        this._textLines[i],
        left + leftOffset,
        top + lineHeights + maxHeight,
        i,
      );
      lineHeights += heightOfLine;
    }
    ctx.restore();
  }

  /**
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  _renderTextFill(ctx: CanvasRenderingContext2D) {
    if (!this.fill && !this.styleHas(FILL)) {
      return;
    }

    this._renderTextCommon(ctx, 'fillText');
  }

  /**
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  _renderTextStroke(ctx: CanvasRenderingContext2D) {
    if ((!this.stroke || this.strokeWidth === 0) && this.isEmptyStyles()) {
      return;
    }

    if (this.shadow && !this.shadow.affectStroke) {
      this._removeShadow(ctx);
    }

    ctx.save();
    this._setLineDash(ctx, this.strokeDashArray);
    ctx.beginPath();
    this._renderTextCommon(ctx, 'strokeText');
    ctx.closePath();
    ctx.restore();
  }

  /**
   * @private
   * @param {String} method fillText or strokeText.
   * @param {CanvasRenderingContext2D} ctx Context to render on
   * @param {Array} line Content of the line, splitted in an array by grapheme
   * @param {Number} left
   * @param {Number} top
   * @param {Number} lineIndex
   */
  _renderChars(
    method: 'fillText' | 'strokeText',
    ctx: CanvasRenderingContext2D,
    line: Array<any>,
    left: number,
    top: number,
    lineIndex: number,
  ) {
    const lineHeight = this.getHeightOfLine(lineIndex),
      isJustify = this.textAlign.includes(JUSTIFY),
      path = this.path,
      isLtr = this.direction === 'ltr',
      sign = this.direction === 'ltr' ? 1 : -1,
      // this was changed in the PR #7674
      // currentDirection = ctx.canvas.getAttribute('dir');
      currentDirection = ctx.direction;

    // Check if we should use BiDi-aware rendering with pre-calculated positions
    // This is needed for advanced layout with RTL or mixed BiDi text
    const chars = this.__charBounds[lineIndex];
    const hasRenderLeft = this.enableAdvancedLayout && chars?.length > 0 && chars[0].renderLeft !== undefined;
    // Disable individual char rendering for now as it breaks Arabic shaping (ligatures)
    // We still need hasRenderLeft to remain true for hit-testing logic in IText
    const useBiDiRendering = false; // hasRenderLeft && !isLtr;

    const shortCut =
      !useBiDiRendering &&
      !isJustify &&
      this.charSpacing === 0 &&
      this.isEmptyStyles(lineIndex) &&
      !path;

    let actualStyle,
      nextStyle,
      charsToRender = '',
      charBox,
      boxWidth = 0,
      timeToRender,
      drawingLeft;

    ctx.save();

    // For BiDi rendering with pre-calculated positions, disable browser BiDi
    // and render each character at its calculated visual position
    if (useBiDiRendering) {
      ctx.canvas.setAttribute('dir', 'ltr');
      ctx.direction = 'ltr';
      ctx.textAlign = LEFT;
    } else if (currentDirection !== this.direction) {
      ctx.canvas.setAttribute('dir', isLtr ? 'ltr' : 'rtl');
      ctx.direction = isLtr ? 'ltr' : 'rtl';

      // Set text alignment based on direction
      // For RTL, use RIGHT alignment so x-coordinate specifies the right edge (where RTL text starts)
      // For LTR, use LEFT alignment so x-coordinate specifies the left edge (where LTR text starts)
      ctx.textAlign = isLtr ? LEFT : RIGHT;
    }
    top -= (lineHeight * this._fontSizeFraction) / this.lineHeight;

    // BiDi rendering: render each character at its pre-calculated visual position
    // renderLeft is relative to line start (0 to lineWidth), and 'left' already includes
    // the line left offset from _renderTextLine, so just add renderLeft to left
    // Note: This renders chars individually which may affect Arabic contextual shaping
    // A more sophisticated approach would group consecutive chars into runs
    if (useBiDiRendering) {
      for (let i = 0; i < line.length; i++) {
        charBox = chars[i] as Required<GraphemeBBox>;
        const renderX = charBox.renderLeft !== undefined ? charBox.renderLeft : charBox.left;
        // Render each character at its visual position
        // layoutText provides renderLeft relative to the visual left edge of the bounding box
        // So we start from -this.width / 2 (visual left) and add renderLeft
        this._renderChar(method, ctx, lineIndex, i, line[i], -this.width / 2 + renderX, top);
      }
      ctx.restore();
      return;
    }

    if (shortCut) {
      // render all the line in one pass without checking
      // drawingLeft = isLtr ? left : left - this.getLineWidth(lineIndex);
      this._renderChar(method, ctx, lineIndex, 0, line.join(''), left, top);
      ctx.restore();
      return;
    }
    // Debug: Log charBounds being used for first line only during justify
    if (isJustify && lineIndex === 0 && method === 'fillText') {
      // console.log(`\n=== RENDER _renderChars line ${lineIndex} ===`);
      // console.log('Initial left:', left.toFixed(2), 'sign:', sign);
      // console.log('_justifyApplied flag:', (this as any)._justifyApplied);
      const lineBounds = this.__charBounds[lineIndex];
      const totalKW = lineBounds?.reduce((s, b) => s + (b?.kernedWidth || 0), 0) || 0;
      // console.log('Total kernedWidth in charBounds:', totalKW.toFixed(2), '(should be ~300 if justify was applied)');
      // Log first few space widths to verify expansion
      const spaceIndices = [3, 9, 15, 23, 31];
      spaceIndices.forEach(idx => {
        const b = lineBounds?.[idx];
        if (b) console.log(`  Space at idx ${idx}: kernedWidth=${b.kernedWidth?.toFixed(2)}`);
      });
    }

    for (let i = 0, len = line.length - 1; i <= len; i++) {
      timeToRender = i === len || this.charSpacing || path;
      charsToRender += line[i];
      charBox = this.__charBounds[lineIndex][i] as Required<GraphemeBBox>;
      if (boxWidth === 0) {
        if (isLtr) {
          // For LTR, adjust for kerning difference of first character
          left += sign * (charBox.kernedWidth - charBox.width);
          boxWidth += charBox.width;
        } else {
          // For RTL with drawingLeft = left - boxWidth positioning,
          // use kernedWidth consistently to ensure the right edge is exactly at 'left'
          boxWidth += charBox.kernedWidth;
        }
      } else {
        boxWidth += charBox.kernedWidth;
      }
      if (isJustify && !timeToRender) {
        if (this._reSpaceAndTab.test(line[i])) {
          timeToRender = true;
        }
      }
      if (!timeToRender) {
        // if we have charSpacing, we render char by char
        actualStyle =
          actualStyle || this.getCompleteStyleDeclaration(lineIndex, i);
        nextStyle = this.getCompleteStyleDeclaration(lineIndex, i + 1);
        timeToRender = hasStyleChanged(actualStyle, nextStyle, false);
      }
      if (timeToRender) {
        if (path) {
          ctx.save();
          ctx.translate(charBox.renderLeft, charBox.renderTop);
          ctx.rotate(charBox.angle);
          this._renderChar(
            method,
            ctx,
            lineIndex,
            i,
            charsToRender,
            -boxWidth / 2,
            0,
          );
          ctx.restore();
        } else {
          // For LTR with textAlign='left': x is the left edge, so drawingLeft = left
          // For RTL with textAlign='right': x is the right edge, so drawingLeft = left
          // Both cases: drawingLeft = left (the text alignment handles the edge correctly)
          drawingLeft = left;
          // Debug: log first chunk positioning for justify
          if (isJustify && lineIndex === 0 && method === 'fillText' && i < 5) {
            // console.log(`  Chunk ending at char ${i}: left=${left.toFixed(2)}, boxWidth=${boxWidth.toFixed(2)}, drawingLeft=${drawingLeft.toFixed(2)}, textAlign=${isLtr ? 'left' : 'right'}`);
          }
          this._renderChar(
            method,
            ctx,
            lineIndex,
            i,
            charsToRender,
            drawingLeft,
            top,
          );
        }
        charsToRender = '';
        actualStyle = nextStyle;
        left += sign * boxWidth;
        boxWidth = 0;
      }
    }
    // Debug: log final position for justify
    if (isJustify && lineIndex === 0 && method === 'fillText') {
      // console.log('Final left position after rendering:', left.toFixed(2));
      // console.log('Expected final position:', (sign > 0 ? this.width / 2 : -this.width / 2).toFixed(2));
    }
    ctx.restore();
  }

  /**
   * This function try to patch the missing gradientTransform on canvas gradients.
   * transforming a context to transform the gradient, is going to transform the stroke too.
   * we want to transform the gradient but not the stroke operation, so we create
   * a transformed gradient on a pattern and then we use the pattern instead of the gradient.
   * this method has drawbacks: is slow, is in low resolution, needs a patch for when the size
   * is limited.
   * @private
   * @param {TFiller} filler a fabric gradient instance
   * @return {CanvasPattern} a pattern to use as fill/stroke style
   */
  _applyPatternGradientTransformText(filler: TFiller) {
    // TODO: verify compatibility with strokeUniform
    const width = this.width + this.strokeWidth,
      height = this.height + this.strokeWidth,
      pCanvas = createCanvasElementFor({
        width,
        height,
      }),
      pCtx = pCanvas.getContext('2d')!;
    pCanvas.width = width;
    pCanvas.height = height;
    pCtx.beginPath();
    pCtx.moveTo(0, 0);
    pCtx.lineTo(width, 0);
    pCtx.lineTo(width, height);
    pCtx.lineTo(0, height);
    pCtx.closePath();
    pCtx.translate(width / 2, height / 2);
    pCtx.fillStyle = filler.toLive(pCtx)!;
    this._applyPatternGradientTransform(pCtx, filler);
    pCtx.fill();
    return pCtx.createPattern(pCanvas, 'no-repeat')!;
  }

  handleFiller<T extends 'fill' | 'stroke'>(
    ctx: CanvasRenderingContext2D,
    property: `${T}Style`,
    filler: TFiller | string,
  ): { offsetX: number; offsetY: number } {
    let offsetX: number, offsetY: number;
    if (isFiller(filler)) {
      if (
        (filler as Gradient<'linear'>).gradientUnits === 'percentage' ||
        (filler as Gradient<'linear'>).gradientTransform ||
        (filler as Pattern).patternTransform
      ) {
        // need to transform gradient in a pattern.
        // this is a slow process. If you are hitting this codepath, and the object
        // is not using caching, you should consider switching it on.
        // we need a canvas as big as the current object caching canvas.
        offsetX = -this.width / 2;
        offsetY = -this.height / 2;
        ctx.translate(offsetX, offsetY);
        ctx[property] = this._applyPatternGradientTransformText(filler);
        return { offsetX, offsetY };
      } else {
        // is a simple gradient or pattern
        ctx[property] = filler.toLive(ctx)!;
        return this._applyPatternGradientTransform(ctx, filler);
      }
    } else {
      // is a color
      ctx[property] = filler;
    }
    return { offsetX: 0, offsetY: 0 };
  }

  /**
   * This function prepare the canvas for a stroke style, and stroke and strokeWidth
   * need to be sent in as defined
   * @param {CanvasRenderingContext2D} ctx
   * @param {CompleteTextStyleDeclaration} style with stroke and strokeWidth defined
   * @returns
   */
  _setStrokeStyles(
    ctx: CanvasRenderingContext2D,
    {
      stroke,
      strokeWidth,
    }: Pick<CompleteTextStyleDeclaration, 'stroke' | 'strokeWidth'>,
  ) {
    ctx.lineWidth = strokeWidth;
    ctx.lineCap = this.strokeLineCap;
    ctx.lineDashOffset = this.strokeDashOffset;
    ctx.lineJoin = this.strokeLineJoin;
    ctx.miterLimit = this.strokeMiterLimit;
    return this.handleFiller(ctx, 'strokeStyle', stroke!);
  }

  /**
   * This function prepare the canvas for a ill style, and fill
   * need to be sent in as defined
   * @param {CanvasRenderingContext2D} ctx
   * @param {CompleteTextStyleDeclaration} style with ill defined
   * @returns
   */
  _setFillStyles(ctx: CanvasRenderingContext2D, { fill }: Pick<this, 'fill'>) {
    return this.handleFiller(ctx, 'fillStyle', fill!);
  }

  /**
   * @private
   * @param {String} method
   * @param {CanvasRenderingContext2D} ctx Context to render on
   * @param {Number} lineIndex
   * @param {Number} charIndex
   * @param {String} _char
   * @param {Number} left Left coordinate
   * @param {Number} top Top coordinate
   * @param {Number} lineHeight Height of the line
   */
  _renderChar(
    method: 'fillText' | 'strokeText',
    ctx: CanvasRenderingContext2D,
    lineIndex: number,
    charIndex: number,
    _char: string,
    left: number,
    top: number,
  ) {
    const decl = this._getStyleDeclaration(lineIndex, charIndex),
      fullDecl = this.getCompleteStyleDeclaration(lineIndex, charIndex),
      shouldFill = method === 'fillText' && fullDecl.fill,
      shouldStroke =
        method === 'strokeText' && fullDecl.stroke && fullDecl.strokeWidth;

    if (!shouldStroke && !shouldFill) {
      return;
    }
    ctx.save();

    ctx.font = this._getFontDeclaration(fullDecl);

    if (decl.textBackgroundColor) {
      this._removeShadow(ctx);
    }
    if (decl.deltaY) {
      top += decl.deltaY;
    }

    if (shouldFill) {
      const fillOffsets = this._setFillStyles(ctx, fullDecl);
      ctx.fillText(
        _char,
        left - fillOffsets.offsetX,
        top - fillOffsets.offsetY,
      );
    }

    if (shouldStroke) {
      const strokeOffsets = this._setStrokeStyles(ctx, fullDecl);
      ctx.strokeText(
        _char,
        left - strokeOffsets.offsetX,
        top - strokeOffsets.offsetY,
      );
    }

    ctx.restore();
  }

  /**
   * Turns the character into a 'superior figure' (i.e. 'superscript')
   * @param {Number} start selection start
   * @param {Number} end selection end
   */
  setSuperscript(start: number, end: number) {
    this._setScript(start, end, this.superscript);
  }

  /**
   * Turns the character into an 'inferior figure' (i.e. 'subscript')
   * @param {Number} start selection start
   * @param {Number} end selection end
   */
  setSubscript(start: number, end: number) {
    this._setScript(start, end, this.subscript);
  }

  /**
   * Applies 'schema' at given position
   * @private
   * @param {Number} start selection start
   * @param {Number} end selection end
   * @param {Number} schema
   */
  protected _setScript(
    start: number,
    end: number,
    schema: {
      size: number;
      baseline: number;
    },
  ) {
    const loc = this.get2DCursorLocation(start, true),
      fontSize = this.getValueOfPropertyAt(
        loc.lineIndex,
        loc.charIndex,
        'fontSize',
      ),
      dy = this.getValueOfPropertyAt(loc.lineIndex, loc.charIndex, 'deltaY'),
      style = {
        fontSize: fontSize * schema.size,
        deltaY: dy + fontSize * schema.baseline,
      };
    this.setSelectionStyles(style, start, end);
  }

  /**
   * @private
   * @param {Number} lineIndex index text line
   * @return {Number} Line left offset
   */
  _getLineLeftOffset(lineIndex: number): number {
    const lineWidth = this.getLineWidth(lineIndex),
      lineDiff = this.width - lineWidth,
      textAlign = this.textAlign,
      direction = this.direction,
      isEndOfWrapping = this.isEndOfWrapping(lineIndex);
    const hasTextAfter = this._textLines
      .slice(lineIndex + 1)
      .some((line) => {
        const lineText = Array.isArray(line) ? line.join('') : line;
        return /\S/.test(lineText);
      });
    const isVisualLastLine = !hasTextAfter;
    let leftOffset = 0;

    // Determine if this line should be justified (not last line)
    const isJustifyLine =
      textAlign === JUSTIFY ||
      (textAlign === JUSTIFY_CENTER && !isEndOfWrapping && !isVisualLastLine) ||
      (textAlign === JUSTIFY_RIGHT && !isEndOfWrapping && !isVisualLastLine) ||
      (textAlign === JUSTIFY_LEFT && !isEndOfWrapping && !isVisualLastLine);

    if (isJustifyLine) {
      // Justified lines start at the left edge (offset 0) for both LTR and RTL
      // The space expansion from enlargeSpaces() fills the line to the full width
      return 0;
    }

    // Handle last line alignment (or non-justify alignments)
    if (textAlign === CENTER || textAlign === JUSTIFY_CENTER) {
      leftOffset = lineDiff / 2;
    } else if (textAlign === RIGHT || textAlign === JUSTIFY_RIGHT) {
      leftOffset = lineDiff;
    }
    // LEFT and JUSTIFY_LEFT have leftOffset = 0, which is the default

    // Apply RTL adjustments for non-justified lines
    if (direction === 'rtl') {
      if (textAlign === RIGHT || textAlign === JUSTIFY || textAlign === JUSTIFY_RIGHT) {
        leftOffset = 0;
      } else if (textAlign === LEFT || textAlign === JUSTIFY_LEFT) {
        leftOffset = -lineDiff;
      } else if (textAlign === CENTER || textAlign === JUSTIFY_CENTER) {
        leftOffset = -lineDiff / 2;
      }
      // Clamp to 0 for overflow cases
      if (lineDiff <= 0) {
        leftOffset = 0;
      }
    }

    return leftOffset;
  }

  /**
   * @private
   */
  _clearCache() {
    // console.log(' _clearCache called');
    // console.trace(' _clearCache stack trace');
    this._forceClearCache = false;
    this.__lineWidths = [];
    this.__lineHeights = [];
    this.__charBounds = [];
    this.__kashidaInfo = [];
    // Reset justify applied flag
    (this as any)._justifyApplied = false;
    // Reset dimension state to force recalculation
    (this as any)._lastDimensionState = null;
  }

  /**
   * Convert a display character index (in _textLines with tatweels) to original text index.
   * When kashida is applied, _textLines contains extra tatweel characters that don't exist
   * in the original text. This method maps back to the original index.
   * @param lineIndex - The line index
   * @param displayCharIndex - Character index in the display text (with tatweels)
   * @returns Original character index (without tatweels)
   */
  _displayToOriginalIndex(lineIndex: number, displayCharIndex: number): number {
    // console.log(` _displayToOriginalIndex called: line=${lineIndex}, displayIdx=${displayCharIndex}`);
    // console.log(` __kashidaInfo exists: ${!!this.__kashidaInfo}, length: ${this.__kashidaInfo?.length}`);
    // console.log(` __kashidaInfo raw:`, JSON.stringify(this.__kashidaInfo));

    const kashidaInfo = this.__kashidaInfo?.[lineIndex];
    if (!kashidaInfo || kashidaInfo.length === 0) {
      // No kashida on this line, indices are the same
      // console.log(` No kashida info for line ${lineIndex}, returning same index`);
      return displayCharIndex;
    }

    // Sort kashida info by charIndex ascending for proper traversal
    const sortedKashida = [...kashidaInfo].sort((a, b) => a.charIndex - b.charIndex);

    // console.log(` _displayToOriginalIndex: line=${lineIndex}, displayIdx=${displayCharIndex}`);
    // console.log(` kashidaInfo:`, sortedKashida.map(k => `{charIdx:${k.charIndex}, cnt:${k.tatweelCount}}`).join(', '));

    let tatweelsBeforeIndex = 0;

    for (const k of sortedKashida) {
      const tatweelCount = k.tatweelCount || 0;
      // Position where tatweels start (after the original character)
      const tatweelStartPos = k.charIndex + 1 + tatweelsBeforeIndex;
      const tatweelEndPos = tatweelStartPos + tatweelCount;

      // console.log(`   k.charIndex=${k.charIndex}, tatweelStartPos=${tatweelStartPos}, tatweelEndPos=${tatweelEndPos}, tatweelsBeforeIndex=${tatweelsBeforeIndex}`);

      if (displayCharIndex < tatweelStartPos) {
        // Before this kashida point
        // console.log(`   displayIdx < tatweelStartPos, break`);
        break;
      } else if (displayCharIndex < tatweelEndPos) {
        // Within tatweel characters - map to the character before tatweels
        // console.log(`   Within tatweel, return ${k.charIndex + 1}`);
        return k.charIndex + 1;
      } else {
        // After this kashida point
        tatweelsBeforeIndex += tatweelCount;
        // console.log(`   After this kashida, tatweelsBeforeIndex now=${tatweelsBeforeIndex}`);
      }
    }

    // Subtract all tatweels that come before this position
    const result = displayCharIndex - tatweelsBeforeIndex;
    // console.log(` Final result: ${displayCharIndex} - ${tatweelsBeforeIndex} = ${result}`);
    return result;
  }

  /**
   * Convert an original text character index to display index (in _textLines with tatweels).
   * @param lineIndex - The line index
   * @param originalCharIndex - Character index in the original text (without tatweels)
   * @returns Display character index (with tatweels)
   */
  _originalToDisplayIndex(lineIndex: number, originalCharIndex: number): number {
    const kashidaInfo = this.__kashidaInfo?.[lineIndex];
    if (!kashidaInfo || kashidaInfo.length === 0) {
      // No kashida on this line, indices are the same
      return originalCharIndex;
    }

    // Sort kashida info by charIndex ascending
    const sortedKashida = [...kashidaInfo].sort((a, b) => a.charIndex - b.charIndex);

    let tatweelsBeforeIndex = 0;

    for (const k of sortedKashida) {
      const tatweelCount = k.tatweelCount || 0;
      // If the original char index is after this kashida insertion point,
      // add the tatweels to the offset
      if (originalCharIndex > k.charIndex) {
        tatweelsBeforeIndex += tatweelCount;
      } else {
        break;
      }
    }

    return originalCharIndex + tatweelsBeforeIndex;
  }

  /**
   * Check if a display character index points to a tatweel character.
   * @param lineIndex - The line index
   * @param displayCharIndex - Character index in the display text
   * @returns True if the character at this index is a tatweel
   */
  _isTatweelAtDisplayIndex(lineIndex: number, displayCharIndex: number): boolean {
    const kashidaInfo = this.__kashidaInfo?.[lineIndex];
    if (!kashidaInfo || kashidaInfo.length === 0) {
      return false;
    }

    // Sort kashida info by charIndex ascending
    const sortedKashida = [...kashidaInfo].sort((a, b) => a.charIndex - b.charIndex);

    let tatweelsBeforeIndex = 0;

    for (const k of sortedKashida) {
      const tatweelCount = k.tatweelCount || 0;
      const tatweelStartPos = k.charIndex + 1 + tatweelsBeforeIndex;
      const tatweelEndPos = tatweelStartPos + tatweelCount;

      if (displayCharIndex >= tatweelStartPos && displayCharIndex < tatweelEndPos) {
        return true;
      }

      tatweelsBeforeIndex += tatweelCount;
    }

    return false;
  }

  /**
   * Get the total number of tatweel characters inserted in a line.
   * @param lineIndex - The line index
   * @returns Total number of tatweels in this line
   */
  _getTatweelCountForLine(lineIndex: number): number {
    const kashidaInfo = this.__kashidaInfo?.[lineIndex];
    if (!kashidaInfo || kashidaInfo.length === 0) {
      return 0;
    }
    return kashidaInfo.reduce((sum, k) => sum + (k.tatweelCount || 0), 0);
  }

  /**
   * Get the original line length (without tatweels).
   * When kashida is applied, _textLines contains extra tatweel characters.
   * This returns the length as it would be in the original text.
   * @param lineIndex - The line index
   * @returns Original line length without tatweels
   */
  _getOriginalLineLength(lineIndex: number): number {
    const displayLength = this._textLines[lineIndex]?.length || 0;
    return displayLength - this._getTatweelCountForLine(lineIndex);
  }

  /**
   * Measure a single line given its index. Used to calculate the initial
   * text bounding box. The values are calculated and stored in __lineWidths cache.
   * @private
   * @param {Number} lineIndex line number
   * @return {Number} Line width
   */
  getLineWidth(lineIndex: number): number {
    if (this.__lineWidths[lineIndex] !== undefined) {
      return this.__lineWidths[lineIndex];
    }

    const { width } = this.measureLine(lineIndex);
    this.__lineWidths[lineIndex] = width;
    return width;
  }

  _getWidthOfCharSpacing() {
    if (this.charSpacing !== 0) {
      return (this.fontSize * this.charSpacing) / 1000;
    }
    return 0;
  }

  /**
   * Retrieves the value of property at given character position
   * @param {Number} lineIndex the line number
   * @param {Number} charIndex the character number
   * @param {String} property the property name
   * @returns the value of 'property'
   */
  getValueOfPropertyAt<T extends StylePropertiesType>(
    lineIndex: number,
    charIndex: number,
    property: T,
  ): this[T] {
    const charStyle = this._getStyleDeclaration(lineIndex, charIndex);
    return (charStyle[property] ?? this[property]) as this[T];
  }

  /**
   * @private
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  _renderTextDecoration(
    ctx: CanvasRenderingContext2D,
    type: 'underline' | 'linethrough' | 'overline',
  ) {
    if (!this[type] && !this.styleHas(type)) {
      return;
    }
    let topOffset = this._getTopOffset();
    const leftOffset = this._getLeftOffset(),
      path = this.path,
      charSpacing = this._getWidthOfCharSpacing(),
      offsetAligner =
        type === 'linethrough' ? 0.5 : type === 'overline' ? 1 : 0,
      offsetY = this.offsets[type];
    for (let i = 0, len = this._textLines.length; i < len; i++) {
      const heightOfLine = this.getHeightOfLine(i);
      if (!this[type] && !this.styleHas(type, i)) {
        topOffset += heightOfLine;
        continue;
      }
      const line = this._textLines[i];
      const maxHeight = heightOfLine / this.lineHeight;
      const lineLeftOffset = this._getLineLeftOffset(i);
      let boxStart = 0;
      let boxWidth = 0;
      let lastDecoration = this.getValueOfPropertyAt(i, 0, type);
      let lastFill = this.getValueOfPropertyAt(i, 0, FILL);
      let lastTickness = this.getValueOfPropertyAt(
        i,
        0,
        TEXT_DECORATION_THICKNESS,
      );
      let currentDecoration = lastDecoration;
      let currentFill = lastFill;
      let currentTickness = lastTickness;
      const top = topOffset + maxHeight * (1 - this._fontSizeFraction);
      let size = this.getHeightOfChar(i, 0);
      let dy = this.getValueOfPropertyAt(i, 0, 'deltaY');
      for (let j = 0, jlen = line.length; j < jlen; j++) {
        const charBox = this.__charBounds[i][j] as Required<GraphemeBBox>;
        currentDecoration = this.getValueOfPropertyAt(i, j, type);
        currentFill = this.getValueOfPropertyAt(i, j, FILL);
        currentTickness = this.getValueOfPropertyAt(
          i,
          j,
          TEXT_DECORATION_THICKNESS,
        );
        const currentSize = this.getHeightOfChar(i, j);
        const currentDy = this.getValueOfPropertyAt(i, j, 'deltaY');
        if (path && currentDecoration && currentFill) {
          const finalTickness = (this.fontSize * currentTickness) / 1000;
          ctx.save();
          // bug? verify lastFill is a valid fill here.
          ctx.fillStyle = lastFill as string;
          ctx.translate(charBox.renderLeft, charBox.renderTop);
          ctx.rotate(charBox.angle);
          ctx.fillRect(
            -charBox.kernedWidth / 2,
            offsetY * currentSize + currentDy - offsetAligner * finalTickness,
            charBox.kernedWidth,
            finalTickness,
          );
          ctx.restore();
        } else if (
          (currentDecoration !== lastDecoration ||
            currentFill !== lastFill ||
            currentSize !== size ||
            currentTickness !== lastTickness ||
            currentDy !== dy) &&
          boxWidth > 0
        ) {
          const finalTickness = (this.fontSize * lastTickness) / 1000;
          let drawStart = leftOffset + lineLeftOffset + boxStart;
          if (this.direction === 'rtl') {
            drawStart = this.width - drawStart - boxWidth;
          }
          if (lastDecoration && lastFill && lastTickness) {
            // bug? verify lastFill is a valid fill here.
            ctx.fillStyle = lastFill as string;
            ctx.fillRect(
              drawStart,
              top + offsetY * size + dy - offsetAligner * finalTickness,
              boxWidth,
              finalTickness,
            );
          }
          boxStart = charBox.left;
          boxWidth = charBox.width;
          lastDecoration = currentDecoration;
          lastTickness = currentTickness;
          lastFill = currentFill;
          size = currentSize;
          dy = currentDy;
        } else {
          boxWidth += charBox.kernedWidth;
        }
      }
      let drawStart = leftOffset + lineLeftOffset + boxStart;
      if (this.direction === 'rtl') {
        drawStart = this.width - drawStart - boxWidth;
      }
      ctx.fillStyle = currentFill as string;
      const finalTickness = (this.fontSize * currentTickness) / 1000;
      currentDecoration &&
        currentFill &&
        currentTickness &&
        ctx.fillRect(
          drawStart,
          top + offsetY * size + dy - offsetAligner * finalTickness,
          boxWidth - charSpacing,
          finalTickness,
        );
      topOffset += heightOfLine;
    }
    // if there is text background color no
    // other shadows should be casted
    this._removeShadow(ctx);
  }

  /**
   * return font declaration string for canvas context
   * @param {Object} [styleObject] object
   * @returns {String} font declaration formatted for canvas context.
   */
  _getFontDeclaration(
    {
      fontFamily = this.fontFamily,
      fontStyle = this.fontStyle,
      fontWeight = this.fontWeight,
      fontSize = this.fontSize,
    }: Partial<
      Pick<
        TextStyleDeclaration,
        'fontFamily' | 'fontStyle' | 'fontWeight' | 'fontSize'
      >
    > = {},
    forMeasuring?: boolean,
  ): string {
    let parsedFontFamily =
      fontFamily.includes("'") ||
        fontFamily.includes('"') ||
        fontFamily.includes(',') ||
        FabricText.genericFonts.includes(fontFamily.toLowerCase())
        ? fontFamily
        : `"${fontFamily}"`;

    // For fonts like STV that don't support English/Latin characters,
    // add fallback fonts for consistent rendering of unsupported characters
    // Only add fallbacks during actual rendering, not for measurements
    if (!forMeasuring && // Only during rendering, not measuring
      !fontFamily.includes(',') && // Don't add fallbacks if already has them
      (fontFamily.toLowerCase().includes('stv') ||
        fontFamily.toLowerCase().includes('arabic') ||
        fontFamily.toLowerCase().includes('naskh') ||
        fontFamily.toLowerCase().includes('kufi'))) {
      // Add fallback fonts for unsupported characters (spaces, punctuation, etc.)
      parsedFontFamily = `${parsedFontFamily}, "Arial Unicode MS", Arial, sans-serif`;
    }

    return [
      fontStyle,
      fontWeight,
      `${forMeasuring ? this.CACHE_FONT_SIZE : fontSize}px`,
      parsedFontFamily,
    ].join(' ');
  }

  /**
   * Renders text instance on a specified context
   * @param {CanvasRenderingContext2D} ctx Context to render on
   */
  render(ctx: CanvasRenderingContext2D) {
    if (!this.visible) {
      return;
    }
    if (
      this.canvas &&
      this.canvas.skipOffscreen &&
      !this.group &&
      !this.isOnScreen()
    ) {
      return;
    }
    if (this._forceClearCache) {
      this.initDimensions();
    }
    super.render(ctx);
  }

  /**
   * Override this method to customize grapheme splitting
   * @todo the util `graphemeSplit` needs to be injectable in some way.
   * is more comfortable to inject the correct util rather than having to override text
   * in the middle of the prototype chain
   * @param {string} value
   * @returns {string[]} array of graphemes
   */
  graphemeSplit(value: string): string[] {
    return graphemeSplit(value);
  }

  /**
   * Returns the text as an array of lines.
   * @param {String} text text to split
   * @returns  Lines in the text
   */
  _splitTextIntoLines(text: string): TextLinesInfo {
    const lines = text.split(this._reNewline),
      newLines = new Array<string[]>(lines.length),
      newLine = ['\n'];
    let newText: string[] = [];
    for (let i = 0; i < lines.length; i++) {
      // Use BiDi-aware grapheme splitting for RTL text
      if (this.direction === 'rtl' || this._containsArabicText(lines[i])) {
        newLines[i] = segmentGraphemes(lines[i]);
      } else {
        newLines[i] = this.graphemeSplit(lines[i]);
      }
      newText = newText.concat(newLines[i], newLine);
    }
    newText.pop();
    return {
      _unwrappedLines: newLines,
      lines: lines,
      graphemeText: newText,
      graphemeLines: newLines,
    };
  }

  /**
   * Check if text contains Arabic characters
   * @private
   */
  _containsArabicText(text: string): boolean {
    return /[\u0600-\u06FF\u0750-\u077F\uFB50-\uFDFF\uFE70-\uFEFF]/.test(text);
  }

  /**
   * Returns object representation of an instance
   * @param {Array} [propertiesToInclude] Any properties that you might want to additionally include in the output
   * @return {Object} Object representation of an instance
   */
  toObject<
    T extends Omit<Props & TClassProperties<this>, keyof SProps>,
    K extends keyof T = never,
  >(propertiesToInclude: K[] = []): Pick<T, K> & SProps {
    return {
      ...super.toObject([...additionalProps, ...propertiesToInclude] as K[]),
      styles: stylesToArray(this.styles, this.text),
      ...(this.path ? { path: this.path.toObject() } : {}),
    };
  }

  set(key: string | any, value?: any) {
    const { textLayoutProperties } = this.constructor as typeof FabricText;
    super.set(key, value);
    let needsDims = false;
    let isAddingPath = false;
    if (typeof key === 'object') {
      for (const _key in key) {
        if (_key === 'path') {
          this.setPathInfo();
        }
        needsDims = needsDims || textLayoutProperties.includes(_key);
        isAddingPath = isAddingPath || _key === 'path';
      }
    } else {
      needsDims = textLayoutProperties.includes(key);
      isAddingPath = key === 'path';
    }
    if (isAddingPath) {
      this.setPathInfo();
    }
    if (needsDims && this.initialized) {
      // Clear browser lines when layout-affecting properties change
      clearBrowserLines(this);
      this.initDimensions();
      this.setCoords();
    }
    return this;
  }

  /**
   * Returns complexity of an instance
   * @return {Number} complexity
   */
  complexity(): number {
    return 1;
  }

  /**
   * List of generic font families
   * @see https://developer.mozilla.org/en-US/docs/Web/CSS/font-family#generic-name
   */
  static genericFonts = [
    'serif',
    'sans-serif',
    'monospace',
    'cursive',
    'fantasy',
    'system-ui',
    'ui-serif',
    'ui-sans-serif',
    'ui-monospace',
    'ui-rounded',
    'math',
    'emoji',
    'fangsong',
  ];

  /* _FROM_SVG_START_ */

  /**
   * List of attribute names to account for when parsing SVG element (used by {@link FabricText.fromElement})
   * @see: http://www.w3.org/TR/SVG/text.html#TextElement
   */
  static ATTRIBUTE_NAMES = SHARED_ATTRIBUTES.concat(
    'x',
    'y',
    'dx',
    'dy',
    'font-family',
    'font-style',
    'font-weight',
    'font-size',
    'letter-spacing',
    'text-decoration',
    'text-anchor',
  );

  /**
   * Returns FabricText instance from an SVG element (<b>not yet implemented</b>)
   * @param {HTMLElement} element Element to parse
   * @param {Object} [options] Options object
   */
  static async fromElement(
    element: HTMLElement | SVGElement,
    options?: Abortable,
    cssRules?: CSSRules,
  ) {
    const parsedAttributes = parseAttributes(
      element,
      FabricText.ATTRIBUTE_NAMES,
      cssRules,
    );

    const {
      textAnchor = LEFT as typeof LEFT | typeof CENTER | typeof RIGHT,
      textDecoration = '',
      dx = 0,
      dy = 0,
      top = 0,
      left = 0,
      fontSize = DEFAULT_SVG_FONT_SIZE,
      strokeWidth = 1,
      ...restOfOptions
    } = { ...options, ...parsedAttributes };

    const textContent = (element.textContent || '')
      .replace(/^\s+|\s+$|\n+/g, '')
      .replace(/\s+/g, ' ');

    // this code here is probably the usual issue for SVG center find
    // this can later looked at again and probably removed.

    const text = new this(textContent, {
      left: left + dx,
      top: top + dy,
      underline: textDecoration.includes('underline'),
      overline: textDecoration.includes('overline'),
      linethrough: textDecoration.includes('line-through'),
      // we initialize this as 0
      strokeWidth: 0,
      fontSize,
      ...restOfOptions,
    }),
      textHeightScaleFactor = text.getScaledHeight() / text.height,
      lineHeightDiff =
        (text.height + text.strokeWidth) * text.lineHeight - text.height,
      scaledDiff = lineHeightDiff * textHeightScaleFactor,
      textHeight = text.getScaledHeight() + scaledDiff;

    let offX = 0;
    /*
      Adjust positioning:
        x/y attributes in SVG correspond to the bottom-left corner of text bounding box
        fabric output by default at top, left.
    */
    if (textAnchor === CENTER) {
      offX = text.getScaledWidth() / 2;
    }
    if (textAnchor === RIGHT) {
      offX = text.getScaledWidth();
    }
    text.set({
      left: text.left - offX,
      top:
        text.top -
        (textHeight - text.fontSize * (0.07 + text._fontSizeFraction)) /
        text.lineHeight,
      strokeWidth,
    });
    return text;
  }

  /* _FROM_SVG_END_ */

  /**
   * Check if the font is ready for accurate measurements
   * @private
   */
  _isFontReady(): boolean {
    if (typeof document === 'undefined' || !('fonts' in document)) {
      return true; // Assume ready in non-browser environments
    }

    try {
      return document.fonts.check(`${this.fontSize}px ${this.fontFamily}`);
    } catch (e) {
      return true; // Fallback to assuming ready if check fails
    }
  }

  /**
   * Schedule re-initialization after font loads
   * @private
   */
  _scheduleInitAfterFontLoad(): void {
    if (typeof document === 'undefined' || !('fonts' in document)) {
      return;
    }

    // Only schedule if not already waiting
    if ((this as any)._fontLoadScheduled) {
      return;
    }
    (this as any)._fontLoadScheduled = true;

    const fontSpec = `${this.fontSize}px ${this.fontFamily}`;
    document.fonts.load(fontSpec).then(() => {
      (this as any)._fontLoadScheduled = false;
      // Re-initialize dimensions with proper font metrics
      this.initDimensions();

      // Extra step for justify alignment after font loading
      if (this.textAlign && this.textAlign.includes(JUSTIFY)) {
        setTimeout(() => {
          if (this.enlargeSpaces) {
            this.enlargeSpaces();
          }
          this.canvas?.requestRenderAll();
        }, 10);
      } else {
        this.canvas?.requestRenderAll();
      }
    }).catch(() => {
      (this as any)._fontLoadScheduled = false;
    });
  }

  /**
   * Force complete text re-initialization (useful after JSON loading)
   */
  forceTextReinitialization(): void {
    // Clear all caches
    this._clearCache();
    this.dirty = true;

    // Force text splitting to rebuild internal structures
    this._splitText();

    // Re-initialize dimensions
    this.initDimensions();

    // Special handling for justify alignment
    if (this.textAlign && this.textAlign.includes(JUSTIFY)) {
      setTimeout(() => {
        if (this.__charBounds && this.__charBounds.length > 0 && this.enlargeSpaces) {
          this.enlargeSpaces();
          this.canvas?.requestRenderAll();
        }
      }, 10);
    }
  }

  /**
   * Returns FabricText instance from an object representation
   * @param {Object} object plain js Object to create an instance from
   * @returns {Promise<FabricText>}
   */
  static fromObject<
    T extends TOptions<SerializedTextProps>,
    S extends FabricText,
  >(object: T) {
    return this._fromObject<S>(
      {
        ...object,
        styles: stylesFromArray(object.styles || {}, object.text),
      },
      {
        extraParam: 'text',
      },
    ).then((textObject: S) => {
      // Ensure text object is properly initialized after JSON deserialization
      textObject.initialized = true;

      // Force reinitialization to ensure proper layout
      if (textObject._clearCache) {
        textObject._clearCache();
      }
      textObject.dirty = true;

      const fontSpec = `${textObject.fontSize}px ${textObject.fontFamily}`;

      // For custom fonts, ensure they're loaded before initializing dimensions
      if (
        typeof document !== 'undefined' &&
        'fonts' in document &&
        textObject.fontFamily !== 'Arial' &&
        textObject.fontFamily !== 'Times New Roman'
      ) {
        return document.fonts
          .load(fontSpec)
          .then(() => {
            textObject.initialized = true;

            // Special handling for STV fonts which have measurement issues
            const isStvFont = textObject.fontFamily
              ?.toLowerCase()
              .includes('stv');
            if (isStvFont) {
              (textObject as any)._browserWrapCache = null;
              (textObject as any)._lastDimensionState = null;
              (textObject as any)._browserWrapInitialized = false;
              (textObject as any)._usingBrowserWrapping = true;
              (textObject as any).useOverlayEditing = true;

              const reinitWithDelay = (attempt: number) => {
                if ((textObject as any).forceTextReinitialization) {
                  (textObject as any).forceTextReinitialization();
                } else {
                  textObject.initDimensions();
                }
                if (textObject.width < 50 && attempt < 3) {
                  setTimeout(() => reinitWithDelay(attempt + 1), 100 * attempt);
                }
              };
              reinitWithDelay(0);
            } else {
              if ((textObject as any).forceTextReinitialization) {
                (textObject as any).forceTextReinitialization();
              } else {
                textObject.initDimensions();
              }
            }
            return textObject;
          })
          .catch(() => {
            textObject.initialized = true;
            if ((textObject as any).forceTextReinitialization) {
              (textObject as any).forceTextReinitialization();
            } else {
              textObject.initDimensions();
            }
            return textObject;
          });
      } else {
        textObject.initialized = true;
        if ((textObject as any).forceTextReinitialization) {
          (textObject as any).forceTextReinitialization();
        } else {
          textObject.initDimensions();
        }
        return textObject;
      }
    });
  }
}

applyMixins(FabricText, [TextSVGExportMixin]);
classRegistry.setClass(FabricText);
classRegistry.setSVGClass(FabricText);
